<template>
  <v-ons-page>
    <h1>Home Page</h1>
    <v-ons-btn
      modifier="large"
      @click="signIn">Sign In</v-ons-btn>
    <v-ons-btn
      modifier="large"
      @click="registration">Registration</v-ons-btn>
  </v-ons-page>
</template>

<script>
  export default {
    data () {
      return {
      }
    },
    methods: {
      signIn () {
        this.$router.push('/signIn')
      },
      registration () {
        this.$router.push('/registration')
      }
    }
  }
</script>

<style scoped>

</style>



