<template id="App">
  <v-ons-navigator
    :page-stack="pageStack"
    :pop-page="goBack"
  >
  </v-ons-navigator>
</template>

<script>
  export default {
    data() {
      return {
        pageStack: []
      }
    },
    methods: {
      goBack() {
        this.$router.push({ name: this.$route.matched[this.$route.matched.length - 2]. name})
      }
    },
    created () {
      // Define como las rutas van a ser mapeadas en la pila de páginas
      const mapRouteStack = route => this.pageStack = route.matched.map(m => m.components.default)

      // Establece la pagina inicial en pageStack dependiendo de la ruta actual
      // en lugar de siempre poner la página 'Home'
      mapRouteStack(this.$route)

      // Cuando la ruta cambia, resetea pageStack a la ruta siguiente
      this.$router.beforeEach((to, from, next) => mapRouteStack(to) && next())
    }
  }
</script>

<style scoped>
</style>



